nado a un punto de descarga, sea este circular o rectangular (CEPIS, 2002). Existen sedimentadores de flujo horizontal, laminar de placas paralelas (a), alta tasa o tipo colmena (b). De acuerdo con el tipo de sedimentador, varían la velocidad del flujo y los tiempos de retención hidráulica. Se debe verificar el adecuado funcionamiento del sedimentador, realizando extracción periódica de los lodos generados (de forma mecánica o manual), evitando la acumulación y/o fermentación de la materia orgánica, que pueda alterar las características del agua tratada. Los lodos deben ser tratados de acuerdo con los criterios de diseño en cada planta (homogeneización, espesamiento y deshidratación) para finalmente disponerlos conforme a las disposiciones de la autoridad ambiental local establecidas en los Artículos 125 y 126 de la Resolución 330 (2017).")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"titulo-sexto color-acento-botones mb-4"},[_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("Figura 2")]),_vm._v(". Sedimentadores en planta de tratamiento de agua")])]),_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"550px"},attrs:{"src":require("@/assets/curso/tema3/img10.png")}})])])])])]),_c('div',{staticClass:"row",attrs:{"numero":"9","titulo":"Filtración"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Es un proceso de percolación del agua a través de un medio o lecho filtrante. Puede ser filtración por gravedad o presión, y según la velocidad de filtración pueden ser lentos, 7-14 m3/m2/día, y rápidos, mayores a 120 m3/m2/día (Artículo 114 de la Resolución 330 de 2017). La correcta operación del filtro está determinada por el mantenimiento de este, basado en la verificación de la colmatación del lecho filtrante por acumulación del lodo (capa biológica) y su consecuente pérdida de carga, lo que genera una reducción en el caudal de salida. Para esto, se realizan actividades de l